import useFetch from 'react-fetch-hook';
const baseUrl = 'http://premstat.tk/core/api/stats.php?';

const addParams = (url: string, queryParams: any) => {
  let modifiedUrl = url;
  console.log({ queryParams });
  for (let prop in queryParams) {
    if (queryParams.hasOwnProperty(prop)) {
      // handle prop as required
      modifiedUrl = `${modifiedUrl}&${prop}=${queryParams[prop]}`;
    }
    console.log({ modifiedUrl });
  }
  return modifiedUrl;
};

const useFetchApi = (apiPath: string, queryParams?: any) => {
  // TODO: prepare case for queryParams.
  const url = addParams(baseUrl + apiPath, queryParams);
  return useFetch<any>(url);
};

export default useFetchApi;
