import React, { useState, useEffect } from 'react';
import MatchGoalsPresentational from './Presentational';
import useFetchApi from '../../hooks/useFetchApi';
import Spinner from '../../components/Elements/Spinner';

const MatchGoalsContainer = ({ type = 'over' }: { type: string }) => {
  const [loading, setLoading] = useState<boolean>(true);
  const { data, error, isLoading } = useFetchApi('teamstats', {
    type: type,
    search: 2.5,
    limit: 20
  });

  useEffect(() => {
    if (!data || isLoading) {
      setLoading(true);
    } else {
      setLoading(false);
    }
  }, [data, isLoading]);

  if (error) {
    console.log(error);
    return <p>error</p>;
  }
  if (loading) return <Spinner />;
  return (
    <MatchGoalsPresentational data={data} isLoading={isLoading} type={type} />
  );
};

export default MatchGoalsContainer;
